.filtros-container {
    padding: .5rem;
    margin: .5rem;
    width: 80%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #ffffff61;
    border-radius: 15px;
}

.carrucel-container-cat {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    overflow: hidden;
}

.btn-l-carrucel {
    width: 3rem;
    cursor: pointer;
    transition: all .3s;
}

.btn-l-carrucel.disabled, .btn-r-carrucel.disabled {
    opacity: 0.5; 
    pointer-events: none; 
  }

.btn-r-carrucel {
    width: 3rem;
    cursor: pointer;
    transition: all .3s;
}

.btn-l-carrucel:hover,
.btn-r-carrucel:hover {
    scale: 115%;
}

.filtros-list {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 1rem;
    transition: all 1s;
    padding: 1rem;
}

.filtros-item {
    display: flex;
    width: 18rem;
    height: 10rem;
    border-radius: 10px;
    transition: all .3s;
    cursor: pointer;
    overflow: hidden;
    box-shadow: 0px 0px 5px #000;
    flex: 0 0 auto;
}

.filtros-item:hover {
    scale: 105%;
    box-shadow: 0px 0px 10px #000;
    background-color: #fff;
}

.filtros-item>img {
    width: 100%;
    transition: all .3s;
}

.filtros-item:hover img {
    opacity: .4;
}

.filtros-item-text {
    position: absolute;
    z-index: 1;
    opacity: 0;
    transition: all .3s;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    font-weight: bold;
    font-size: 1.2rem;
    width: 18rem;
    height: 10rem;
}

.filtros-item:hover>.filtros-item-text {
    opacity: 1;
}