.contactanos-container {
    padding: .5rem;
    margin: .5rem;
    width: 99%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #ffffff61;
    border-radius: 15px;
}

.form-contacto {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 2rem;
    padding: 1rem;
}

.form-contacto>p{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    text-align: center;
}

.form-contacto>p>label {
    width: 10rem;
}

.form-contacto>p>input {
    width: 15rem;
    padding: .5rem;
    margin: .5rem;
    border: 1px solid #a33939ad;
    border-radius: 5px;
    outline: none;
}

.form-contacto>button {
    padding: .5rem 1rem;
    border: none;
    border-radius: 10px;
    background-color: #a33939ad;
    cursor: pointer;
    color: #fff;
    font-weight: 600;
}

.form-contacto>button:hover {
    background-color: #5f0000e4;
}