.contacto-redes-container {
    padding: .5rem;
    margin: .5rem;
    width: 99%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: #ffffff61;
    border-radius: 15px;
    gap: 2rem;
}

.contacto-redes-container>h3{
    font-size: 2rem;
}

.img-redes {
    width: 80%;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.img-redes>a>img{
    width: 4rem;
    height: 4rem;
}