.principio-index-container {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.principio-index-container>img{
    position: relative;
    width: 100%;
    height: 100dvh;
}

.principio-index-text {
    position: absolute;
    top: 4rem;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 1rem;
    height: 75dvh;
}

.principio-index-text>img{
    height: 65%;
}

.principio-index-text>p{
    color: #000000;
    background-color: #ffffff7e;
    font-size: 1.4rem;
    font-weight: 600;
    width: 50%;
    border-radius: 10px;
}

.principio-index-text>p>strong{
    color: #0a9c00;
}

.principio-index-text>a>button{
    padding: .7rem 1.3rem;
    border-radius: 10px;
    border: none;
    cursor: pointer;
    background-color: #a33939ad;
    transition: all .3s;
}

.principio-index-text>a>button:hover{
    background-color: #5f0000e4;
}

.principio-index-text>a>button{
    font-weight: bold;
    color: #fff;
    text-decoration: none;
    font-size: 1.5rem;
}
